import React, { Component } from 'react';
import { compose } from 'redux';

import Box from 'components/Box'
import Container from 'components/Container'
import Flex from 'components/Flex'
import Text from 'components/Text'
import Link from 'components/Link'
import Title from 'components/Title'
import Button from 'components/Button';
import Image from 'components/Image';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import { mobileOrDesktop } from 'components/ThemeProvider/theme';

import setAnswerHoc from './setAnswerHoc'
import result13 from './result-1-3.svg'
import result46 from './result-4-6.svg'
import result78 from './result-7-8.svg'
import notice from './notice.svg'

import Layout from '../Layout';
import left from '../bg-left.svg'
import right from '../bg-right.svg'

import Sharer from '../NoticePage/Sharer'
import BgDeco from '../MemoPage/BgDeco'
import withResponsive from '../../hoc/withResponsive'

class Result extends Component {
  render() {
    const { isMobile, testAnswers } = this.props
    const datas = Object.values(testAnswers).filter(a => a === true)
    const text = ( datas.length > 3 ? datas.length > 6 ?
      '事先思考自己的身體狀況，擁有正確的健康識能，讓看診更有效率，也別忘了醫師提醒的注意事項，這樣才能讓病情快快好轉喔！' :
      '下次看診時記得使用看診好幫手，事先思考身體狀況、詢問醫生的問題，也別忘了告知醫師過敏情形以及所有症狀，才能讓看診更有效率喔！' :
      '你是不是看診時覺得霧煞煞，常常不知道跟醫生說什麼，還不快使用看診好幫手！你需要事先思考身體狀況、詢問醫生的問題，才能讓醫生能夠更快掌握你的病情！'
    )
    const content = (
      <Container py="2em">
        <Box
          mx={mobileOrDesktop('1em', '15%')}
          pb={mobileOrDesktop('0em', '1em')}
          boxShadow="0px 3px 4px 4px rgba(0,0,0,0.1)"
          borderRadius="1em"
          bg="white"
        >
          <Flex
            bg="questionBg"
            p="1em"
            borderRadius="1em 1em 0 0"
            alignItems="flex-end"
            justifyContent="center"
          >
            <Box fontSize={mobileOrDesktop('4em', '2.75em')} fontWeight="bold" color="titleBlue" >{datas.length}</Box>
            <Box fontSize={mobileOrDesktop('2em', '1.25em')} fontWeight="bold" color="lightPurple" mx="0.5rem">/ {Object.values(testAnswers).length}</Box>
            <Box mx="1em">
              <Text.serif fontSize="1.5em" fontWeight="bold" color="titleBlue">{datas.length > 3 ? datas.length > 6 ? '表現一級棒' : '還可以再更好！' : '再加油！'}</Text.serif>
              <Text color="darkBlue">{datas.length > 6 ? '診間優等生就是你！' : '快使用看診好幫手'}</Text>
            </Box>
          </Flex>
          <Flex p="2em" alignItems="center" flexWrap={mobileOrDesktop('wrap', 'nowrap')}>
            <Image width={mobileOrDesktop(1, 1/2)} mx="1em" src={datas.length > 3 ? datas.length > 6 ? result78 : result46 : result13} />
            <Box
              ml={mobileOrDesktop(0, '0.5em')}
              width={mobileOrDesktop(1, 1/2)}
              mt={mobileOrDesktop('2em', 0)}
              mx={mobileOrDesktop('1em', 0)}
              lineHeight="1.5"
            >{text}</Box>
          </Flex>
        </Box>
        <Flex textAlign="center" alignItems="center" mt="3em" px={mobileOrDesktop('1em', '25%')}>
          <Box width={mobileOrDesktop('12em', '7em')}>
            <Image src={notice} />
          </Box>
          <Box textAlign="left" ml="1em">
            <Text.serif fontSize={mobileOrDesktop('1em', '1.25em')} fontWeight="bold" color="titleBlue">為下次看診做好準備！</Text.serif>
            <Text mt="0.5em" lineHeight="1.4">在生病不舒服的情況下，可能一不小心就會忘記告訴醫生一些細節，現在就下載看診備忘錄，讓你下次看診不再迷糊</Text>
          </Box>
        </Flex>
        <Flex flexDirection={mobileOrDesktop('column', 'row')} justifyContent="center" alignItems="center" my={mobileOrDesktop('1.25em', '2.25em')}>
          <Button width="12em" px="2em" mt="1em" mx="0.25em" is={Link} to="/notice" >看診該注意什麼</Button>
          <Sharer width="12em" mt="1em" mx="0.25em">分享網頁</Sharer>
        </Flex>
      </Container>
    )
    return (
      <Layout>
        {isMobile ? (
          content
        ) : (
          <Box position="relative">
            {content}
            <BgDeco />
          </Box>
        )}
      </Layout>
    );
  }
}

export default compose(setAnswerHoc, withResponsive)(Result);
