import React, { useState } from 'react';
import { StaticQuery } from 'gatsby'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Circle from 'components/Circle';
import Fb from 'components/Fb';
import Line from 'components/Line';
import Button from 'components/Button';
import Image from 'components/Image';
import Text from 'components/Text';
import Modal from 'components/Modal';

import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import share from './share.svg';
// import withResponsive from '../../hoc/withResponsive'

const Sharer = (props) => {
  const [open , setOpen] = useState(0)
  return (
    <StaticQuery
      query={graphql`
      query SiteUrlQuery {
        site {
          siteMetadata {
            url
          }
        }
      }`}
      render={({ site: { siteMetadata: { url } } }) => (
        <div>
          <Box textAlign="center">
            <Button {...props} onClick={() => setOpen(1)} />
          </Box>
          <Modal
            isOpen={Boolean(open)}
            onRequestClose={() => setOpen(0)}
          >
            <Box textAlign="center">
              <Box mx={mobileOrDesktop('15%', '30%')} mb="1em">
                <Image src={share} />
              </Box>
              <Text color="titleBlue" fontSize="1.6em" mt="1em" fontWeight="bold">分享給親友</Text>
              <Text fontSize="1.2em">一起當看診小達人！</Text>
              <Flex justifyContent="center" mt="2em">
                <Box width="5em" mx="0.25em">
                  <Circle is={Button.download} onClick={() => window.open(`http://www.facebook.com/share.php?u=${url}`)}><Fb width="5em" /></Circle>
                </Box>
                <Box width="5em" mx="0.25em">
                  <Circle is={Button.download} onClick={() => window.open(`https://social-plugins.line.me/lineit/share?url=${url}`)}><Line width="5em" /></Circle>
                </Box>
              </Flex>
            </Box>
          </Modal>
        </div>
      )}
    />
  );
};

export default (Sharer);
